.board {
  max-width: 320px;
  height: 20.962px;
  padding: 7px;
  border-radius: 15px;
  border: 1px solid rgba(255, 255, 255, 0.31);
  background: linear-gradient(
    142deg,
    rgba(69, 69, 69, 0.76) -14.45%,
    rgba(0, 0, 0, 0.46) 138.88%
  );
  box-shadow: 0px 3px 4px 0px rgba(0, 0, 0, 0.49) inset;
  backdrop-filter: blur(24.5px);
}

.appSlider__progress {
  width: 100%;
  height: 42px;
}

.clipRect {
  transition: width 0.2s ease-in-out;
}
