.modal {
  position: absolute;
  top: 150px;
  padding: 32px 7px 50px 7px;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: linear-gradient(
    132deg,
    #d10a04 4.76%,
    #210000 34.75%,
    #001e01 65.2%,
    #00a923 94.71%
  );
  border-radius: 20px;
  max-width: 326px;
}

.cross {
  display: flex;
  justify-content: center;
  align-items: center;
  background: transparent;
  border: none;
  width: 19.607px;
  height: 19.607px;
  position: absolute;
  top: 15px;
  right: 15px;
  cursor: pointer;
}

.title {
  color: #fff;
  text-align: center;
  font-family: IQOS;
  font-size: 30px;
  font-style: normal;
  font-weight: 700;
  line-height: 55.7px; /* 185.667% */
  text-transform: uppercase;
  margin-bottom: 14px;
}

.text {
  color: #fff;
  text-align: center;
  font-family: IQOS;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px; /* 125% */
  margin-bottom: 38px;
}

.button {
  border-radius: 24px;
  background: #fff;
  color: #000;
  text-align: center;
  font-family: IQOS;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px; /* 150% */
  border: none;
  padding: 14px 32px;
  width: 265px;
}
