.overlay {
  width: 100%;
  height: 100%;
  padding: 56px 26px 40px 26px;
  background-color: rgba(0, 0, 0, 0.6);
}
.modal {
  background: linear-gradient(
    138deg,
    rgba(0, 0, 0, 0.66) -43.42%,
    rgba(116, 116, 116, 0) 93.97%
  );
  stroke-width: 1.5px;
  border: 1px solid rgba(255, 255, 255, 0.31);
  backdrop-filter: blur(39.5px);

  border-radius: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  padding: 32px 16px 36px;
  z-index: 1;
  opacity: 1;
}

.cross {
  display: flex;
  justify-content: center;
  align-items: center;
  background: transparent;
  border: none;
  width: 19.607px;
  height: 19.607px;
  position: absolute;
  top: 15px;
  right: 15px;
  cursor: pointer;
}

.redCircle {
  position: absolute;
  top: -19px;
}

.title {
  max-width: 320px;
  color: #ff6281;
  text-align: center;
  font-family: IQOS;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 23px; /* 143.75% */
  letter-spacing: 2.24px;
  text-transform: uppercase;
  margin-bottom: 10px;
}

.subtitle {
  color: #fff;
  text-align: center;
  font-family: IQOS;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px; /* 142.857% */
  margin-bottom: 30px;
}

.imgContainer {
  position: relative;
  width: fit-content;
  height: fit-content;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-bottom: 40px;
}

.pack {
  /* position: absolute;
  top: 50px; */
  width: 230px;
  height: 190px;
  z-index: 1;
}

.background {
  position: absolute;
  width: 300px;
  height: 300px;
}

.packText {
  position: absolute;
  bottom: -7px;
  color: #fff;
  text-align: center;
  font-family: IQOS;
  font-size: 10px;
  font-style: normal;
  font-weight: 300;
  line-height: 24px; /* 240% */
}

.modalButton {
  display: flex;
  width: 228px;
  height: 44px;
  padding: 10px 32px 14px 32px;
  justify-content: center;
  align-items: center;
  border-radius: 24px;
  background: #fff;
  border: none;
  color: #000;
  font-family: IQOS;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px; /* 150% */
}
