.guide {
  padding: 80px 30px 50px 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.guideContainer {
  width: 313px;
  height: 394px;
  stroke-width: 1.5px;
  stroke: rgba(255, 255, 255, 0.31);
  backdrop-filter: blur(24.5px);
  background-image: url(../../images//guide/image.svg);
  background-repeat: no-repeat;
  border-radius: 24px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 25px 30px;
}

.title {
  color: #8ddd39;
  text-align: center;
  font-family: IQOS;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 23px; /* 143.75% */
  letter-spacing: 2.24px;
  text-transform: uppercase;
}

.list {
  display: flex;
  flex-direction: column;
  gap: 17px;
}
.text {
  color: #fff;
  font-family: IQOS;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px; /* 128.571% */
}

.button {
  border: none;
  display: flex;
  width: 254px;
  height: 44px;
  justify-content: center;
  align-items: center;
  border-radius: 24px;
  background: #fff;
  color: #000;
  text-align: center;
  font-family: IQOS;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
}
