.overlay {
  width: 100%;
  height: 100%;
  padding: 56px 26px 40px 26px;
  background-color: rgba(0, 0, 0, 0.6);
}
.modal {
  min-height: 520px;
  background: linear-gradient(
    138deg,
    rgba(0, 0, 0, 0.66) -43.42%,
    rgba(116, 116, 116, 0) 93.97%
  );
  background-image: url(../../../images/modals/greenmodal/background.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  stroke-width: 1.5px;
  stroke: rgba(255, 255, 255, 0.31);
  backdrop-filter: blur(39.5px);
  border: 1px solid grey;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  position: relative;
  padding: 32px 16px 36px;
  opacity: 1;
}

.cross {
  display: flex;
  justify-content: center;
  align-items: center;
  background: transparent;
  border: none;
  width: 19.607px;
  height: 19.607px;
  position: absolute;
  top: 15px;
  right: 15px;
  cursor: pointer;
}

.greenCircle {
  position: absolute;
  top: -19px;
}

.title {
  max-width: 320px;
  color: #12e20f;
  text-align: center;
  font-family: IQOS;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 23px; /* 143.75% */
  letter-spacing: 2.24px;
  text-transform: uppercase;
  margin-bottom: 10px;
}

.subtitle {
  color: #fff;
  text-align: center;
  font-family: IQOS;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px; /* 142.857% */
  max-width: 200px;
}

.imgContainer {
  position: relative;
  width: fit-content;
  height: fit-content;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.modalButton {
  display: flex;
  width: 228px;
  height: 44px;
  padding: 10px 32px 14px 32px;
  justify-content: center;
  align-items: center;
  border-radius: 24px;
  background: #fff;
  border: none;
  color: #000;
  font-family: IQOS;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px; /* 150% */
}

.textContainer {
  display: flex;
  flex-direction: column;
  gap: 13px;
  align-items: center;
}
