.header {
  height: 52.2px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: calc(50% - (92px / 2));
  background: #fff;
  box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, 0.06);
  padding-right: 8px;
}

.button {
  justify-self: end;
  padding: 0;
  border: none;
  background-color: transparent;
}

.link {
  color: #34303d;
  font-family: IQOS;
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  line-height: 10px;
  text-decoration: none;
}
