.overlay {
  width: 100%;
  height: 100%;
  padding: 56px 26px 40px 26px;
  background-color: rgba(0, 0, 0, 0.6);
}
.modal {
  min-height: 520px;
  background: linear-gradient(
    138deg,
    rgba(0, 0, 0, 0.66) -43.42%,
    rgba(116, 116, 116, 0) 93.97%
  );

  stroke-width: 1.5px;
  stroke: rgba(255, 255, 255, 0.31);
  backdrop-filter: blur(39.5px);
  border: 1px solid grey;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  position: relative;
  padding: 32px 16px 36px;
  opacity: 1;
}

.cross {
  display: flex;
  justify-content: center;
  align-items: center;
  background: transparent;
  border: none;
  width: 19.607px;
  height: 19.607px;
  position: absolute;
  top: 15px;
  right: 15px;
  cursor: pointer;
}

.whiteCircle {
  position: absolute;
  top: -32px;
}

.title {
  color: #fff;
  font-family: IQOS;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 23px;
  letter-spacing: 2.24px;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 28px;
}
.span {
  color: #89d63c;
}

.stickContainer {
  position: relative;
}

.click {
  position: absolute;
  top: -20px;
  left: -15.5px;
  border: none;
  background: transparent;
  cursor: pointer;
}
.cursorContainer {
  position: absolute;
  right: -100px;
  top: 70px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2px;
}

.cursorText {
  color: #fff;
  font-family: IQOS;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px; /* 150% */
  letter-spacing: 0.96px;
  text-transform: uppercase;
}

.list {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-bottom: 25px;
  list-style: circle;
}

.item {
  color: #fff;
  font-family: IQOS;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px; /* 128.571% */
}

.secondTitle {
  color: #fff;
  text-align: center;
  font-family: IQOS;
  font-size: 34px;
  font-style: normal;
  font-weight: 400;
  line-height: 55.7px; /* 163.824% */
  text-transform: uppercase;
}

.greenBack {
  position: absolute;

  bottom: -150px;
  z-index: -1;
}

.secondStep {
  min-height: 520px;
  background: linear-gradient(
    138deg,
    rgba(0, 0, 0, 0.66) -43.42%,
    rgba(116, 116, 116, 0) 93.97%
  );

  stroke-width: 1.5px;
  stroke: rgba(255, 255, 255, 0.31);
  backdrop-filter: blur(39.5px);
  border: 1px solid grey;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  position: relative;
  padding: 32px 16px 36px;
  opacity: 1;
  background-image: url(../../../images//modals//whitemodal//green-back.png);
  background-repeat: no-repeat;
  background-position: top 10px left -120px;
}
