.gameContainer {
  margin-top: 35px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  width: 350px;
  max-height: 440px;

  background: linear-gradient(
    138deg,
    rgba(0, 0, 0, 0.66) -43.42%,
    rgba(0, 0, 0, 0) 93.97%
  );
  stroke-width: 1.5px;
  border: 1px solid rgba(255, 255, 255, 0.31);
  backdrop-filter: blur(39.5px);
  border-radius: 20px;
  padding: 20px 10px;
}

.game {
  display: flex;
  align-items: center;
  justify-content: center;
}

.board {
  width: 320px;
  height: 320px;
  display: flex;
  flex-wrap: wrap;
  overflow: hidden;
  gap: 5px;
}

.board img {
  width: 60px;
  height: 60px;
}
