.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  padding: 30px 13px;
  position: relative;
}

.title {
  max-width: 300px;
  color: #fff;
  text-align: center;
  font-family: IQOS;
  font-size: 34px;
  font-style: normal;
  font-weight: 400;
  line-height: 55.7px; /* 163.824% */
  text-transform: uppercase;
}

.text {
  max-width: 300px;
  color: #fff;
  text-align: center;
  font-family: IQOS;
  font-size: 13px;
  font-style: normal;
  font-weight: 400;
  line-height: 120%; /* 15.6px */
}

.packs {
  min-width: 313px;
  padding: 37px 20px;
  fill: linear-gradient(
    138deg,
    rgba(0, 0, 0, 0.26) -43.42%,
    rgba(0, 0, 0, 0) 93.97%
  );
  stroke-width: 1.5px;

  backdrop-filter: blur(24.5px);
  border-radius: 20px;
  border: 1px solid rgba(255, 255, 255, 0.31);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 130px;
}

.greenContainer {
  min-width: 271px;
  position: relative;
  display: flex;
  justify-content: flex-end;
}

.greenText {
  width: 130px;
  height: 36px;
  padding: 5px 14px;
  border-radius: 20.551px;
  border: 1.284px solid rgba(255, 255, 255, 0.31);
  background-color: rgba(0, 120, 31, 0.66);
  backdrop-filter: blur(20.978755950927734px);
  color: #fff;
  text-align: center;
  font-family: IQOS;
  font-size: 8.76px;
  font-style: normal;
  font-weight: 700;
  line-height: 12.4px; /* 141.553% */
  letter-spacing: 0.788px;
  text-transform: uppercase;
  position: absolute;
  right: 150px;
  top: -20px;
}
.redContainer {
  position: absolute;
  top: 120px;
  min-width: 271px;
}

.redText {
  width: 130px;
  height: 36px;
  padding: 5px 14px;
  border-radius: 20.551px;
  border: 1.284px solid rgba(255, 255, 255, 0.31);
  background: linear-gradient(
    138deg,
    rgba(134, 18, 5, 0.66) -43.42%,
    rgba(208, 10, 5, 0.66) 93.97%
  );

  backdrop-filter: blur(20.978755950927734px);
  color: #fff;
  text-align: center;
  font-family: IQOS;
  font-size: 8.76px;
  font-style: normal;
  font-weight: 700;
  line-height: 12.4px; /* 141.553% */
  letter-spacing: 0.788px;
  text-transform: uppercase;
  position: absolute;
  left: 150px;
  bottom: -10px;
}

.price {
  width: 118px;
  height: 33px;
  border-radius: 24px;
  border: 1.5px solid rgba(255, 255, 255, 0.31);
  background: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0.26) -4.91%,
    rgba(0, 0, 0, 0) 272.16%
  );
  backdrop-filter: blur(24.5px);
  padding: 8px 10px;
  color: #fff;
  text-align: center;
  font-family: IQOS;
  font-size: 16.793px;
  font-style: normal;
  font-weight: 700;
  line-height: 15.954px; /* 95% */
}
.priseSpan {
  vertical-align: top;
  font-size: 10px;
}
